<template>
  <div class="flipper">
    <transition :name="flip ? 'flip-left' : 'flip-right'" mode="out-in">
      <div v-if="!flip" :key="1">
        <HistorySemiItem :bet="bet" @close="handleflip" />
      </div>
      <div v-else :key="2">
        <HistoryFullItem :bet="bet" @close="handleflip" />
      </div>
    </transition>
  </div>
</template>

<script>
import HistorySemiItem from "../components/HistorySemiItem.vue";
import HistoryFullItem from "../components/HistoryFullItem.vue";
export default {
  data() {
    return {
      flip: false,
    };
  },
  components: {
    HistorySemiItem,
    HistoryFullItem,
  },
  props: {
    bet: {
      type: Object,
      require: true,
    },
  },
  methods: {
    handleflip() {
      this.flip = !this.flip;
    },
  },
};
</script>

<style>
.flipper {
  perspective: 1000px;
  /* position: absolute; */
  transform-style: preserve-3d;
}
.flip-left-enter-active {
  transition: all 0.3s ease-out;
}

.flip-left-leave-active {
  transition: 0.5s ease-in;
}

.flip-left-enter {
  transform: rotateY(-90deg);
}
.flip-left-leave-to {
  transform: rotateY(90deg);
}

.flip-right-enter-active {
  transition: all 0.3s ease-out;
}

.flip-right-leave-active {
  transition: 0.5s ease-in;
}

.flip-right-enter {
  transform: rotateY(90deg);
}
.flip-right-leave-to {
  transform: rotateY(-90deg);
}
</style>
